import * as type from './actions/actionTypes'

const defaultState = {
  isDataLoaded: true,
  userInfo: {},
  buttleList: [],
  enemyButtleList: [],
  enemys: {
    angle: [],
    rectangle: [],
    boss: []
  },
  isModalOpen: false,
  isHitUser: false,
  life: 100,
  isGameOver: false,
  totalKill: 0,
  shield: {},
  rankList: [],
  player: ''
}

const initialState = {...defaultState}

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case type.GET_USER_INFO:
      return {
        ...state,
        isDataLoaded: true,
        userInfo: {...action.userInfo}
      }
    case type.TOGGLE_MODAL:
      return {
        ...state,
        isModalOpen: !state.isModalOpen
      }
    case type.SET_DATA_LOADED:
      return {
        ...state,
        isDataLoaded: action.payload.isDataLoaded
      }
    case type.ADD_NEW_BUTTLE:
      return {
        ...state,
        buttleList: [...state.buttleList, action.payload.buttle]
      }
    case type.DELETE_BUTTLE:
      return {
        ...state,
        buttleList: state.buttleList.filter(buttle => (
          buttle.timestamp !== action.payload.buttle.timestamp
        ))
      }
    case type.ADD_NEW_ENEMY:
      return {
        ...state,
        enemys: {
          ...state.enemys,
          [action.payload.key]: [...state.enemys[action.payload.key], action.payload.enemy]
        }
      }
    case type.DELETE_ENEMY:
      return {
        ...state,
        totalKill: action.payload.byUser ? state.totalKill + 1 : state.totalKill,
        enemys: {
          ...state.enemys,
          [action.payload.key]: state.enemys[action.payload.key].filter(item => (
            item.timestamp !== action.payload.enemy.timestamp
          ))
        }
      }
    case type.UPDATE_ENEMY:
      return {
        ...state,
        enemys: {
          ...state.enemys,
          [action.payload.key]: state.enemys[action.payload.key].map(item => {
            if (item.timestamp === action.payload.enemy.timestamp) {
              return {
                ...item,
                ...action.payload.enemy
              }
            }
            return item
          })
        }
      }
    case type.TOGGLE_HIT_USER:
      return {
        ...state,
        isHitUser: action.payload.isHitUser,
        life: action.payload.isHitUser ? state.life - action.payload.hitPoint : state.life,
        isGameOver: (state.life <= 0)
      }
    case type.ADD_NEW_ENEMY_BUTTLE:
      return {
        ...state,
        enemyButtleList: [...state.enemyButtleList, action.payload.buttle]
      }
    case type.DELETE_ENEMY_BUTTLE:
      return {
        ...state,
        enemyButtleList: state.enemyButtleList.filter(enemyButtle => (
          enemyButtle.timestamp !== action.payload.buttle.timestamp
        ))
      }
    case type.UPDATE_SHIELD:
      return {
        ...state,
        shield: {
          ...state.shield,
          ...action.payload.shield
        }
      }
    case type.RESTART_GAME:
      const {player, rankList, ...otherProps} = defaultState
      return {
        ...state,
        ...otherProps
      }
    case type.GET_RANK_LIST:
    case type.ADD_NEW_RANK:
      return {
        ...state,
        isDataLoaded: true,
        rankList: action.payload.rankList
      }
    case type.UPDATE_PLAYER:
      return {
        ...state,
        player: action.payload.player
      }
    default:
      return state
  }
}

export default reducer



