import React from 'react'
import {Link} from 'react-router-dom'
import { withStyles } from '@material-ui/core/styles'

import AppBar from '@material-ui/core/AppBar'
import IconButton from '@material-ui/core/IconButton'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'

import InfoIcon from '@material-ui/icons/Info'

const styles = theme => ({
  root: {
    zIndex: theme.zIndex.drawer + 1
  },
  header: {
    background: 'transparent',
    boxShadow: 'none'
  },
  viewOnGithub: {
    position: 'fixed',
    top: 0,
    right: 0,
    height: 64,
    zIndex: 1,
    [theme.breakpoints.only('xs')]: {
      height: 56
    }
  }
})

class AppHeader extends React.Component {
  constructor () {
    super()
    this.state = {}
  }

  render () {
    const { classes } = this.props
    return (
      <div className={classes.root}>
        <AppBar position='fixed' className={classes.header}>
          <Toolbar>
            <IconButton component={Link} to='/readme'>
              <InfoIcon color='primary' />
            </IconButton>
            <Typography variant='title' color='inherit'>
              CH07 Canvas Game
            </Typography>
          </Toolbar>
          <a href='https://github.com/sky172839465/the-f2e-ch07-canvas-game' target='block'>
            <img
              className={classes.viewOnGithub}
              src={`https://sky172839465.github.io/the-f2e/img/view-on-github.black.png?now=${Date.now()}`}
              alt='View on github' />
          </a>
        </AppBar>
      </div>
    )
  }
}

AppHeader.propTypes = {}

export default withStyles(styles)(AppHeader)



