import Loadable from 'react-loadable'

import AppLoading from './container/AppLoading'

const lazyloadComponent = (componentName) => {
  return Loadable({
    loader: () => import(`./container/${componentName}`),
    loading: AppLoading
  })
}

const RouteConfig = [
  {
    page: 'Readme',
    path: '/readme',
    component: lazyloadComponent('Readme')
  }, {
    page: 'Game',
    path: '/game',
    component: lazyloadComponent('Game')
  }, {
    page: 'Rank',
    path: '/rank',
    component: lazyloadComponent('Rank')
  }
]

export default RouteConfig



